export default {
	data() {
		return {
		
		}
	},
	onUnload() {
		console.log('页面被卸载了')
	},
	destroyed() {
		console.log('Vue实例被销毁了')
	},
	onLoad() {




		console.log('这段文字只在微信小程序中展示！')

	},
	onPullDownRefresh() {
		
		
	},
	onReachBottom() {
		
	},
	methods:{
		clickHandle(e) {
			uni.request({
				url: 'http://img.mp.itc.cn/upload/20170401/2a003704efe043aca36b9e9c2d8cb48e.gif',
				header: {
					'header-test': 'success'
				},
				method:"GET",
				success:(res)=> {
					console.log('接口返回成功')
				}
			})
		},
		clickSetData() {
			uni.setStorageSync('id',1)
			uni.setStorage({
				key: 'age',
				data: 11,
				success:(res) =>{
					console.log('缓存成功')
				}
			})
		},
		clickgetData() {
			uni.getStorage({
				key: 'age',
				success:(res)=> {
					console.log(res)
				}
			})
			let res = uni.getStorageSync('id');
			console.log(res)
		},
		clickDeletData() {
			uni.removeStorage({
				key: 'id',
				success:(res) =>{
					console.log(res)
				}
			})
			uni.removeStorageSync('age');
		},
		gotoDedail() {
			uni.navigateTo({
				url:'../detail/detail',
				success:() =>{
					console.log('跳转详情页成功')
				}
			})
			
		},
		gotoIndex() {
			uni.switchTab({
				url:'../index/index?id=1&type=httpTest',
				success: () => {
					console.log('跳转至首页')
				}
			})
		}
	}
}
